import replyComment from '@/components/comment-item'
import empty from '@/components/empty'
import { getRoundUp } from '@/api'
export default {
	name: '',
	data () {
		return {
			list: []
		}
	},
	onLoad () {
		this._getRoundUp()
	},
	methods: {
		async _getRoundUp () {
			let result = await getRoundUp ({
				token: uni.getStorageSync('token')
			}) 
			if(result.code == 2000) {
				this.list = result.data;
			}
		}
	},
	components: {
		replyComment,
		empty
	}
}
